#data-container {
  height: 100%;
  width: 100%;
}
#data {
  height: 200px;
  width: 100%;
  border-radius: 0.3rem;
  position: relative;
  border: 1px solid #d3d6da;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
#result-no-answer {
  position: absolute;
  bottom: 50%;
  left: 10%;
  color: aliceblue;
}
#judge {
  position: absolute;
  top: -100%;
  left: 37%;
}
#result-raise,
#result-call,
#result-fold {
  width: 33%;
  text-align: center;
  font-size: 1.3rem;
  z-index: 0;
  position: absolute;
  bottom: 5%;
}
#result-raise {
  left: 2%;
}
#result-call {
  left: 33%;
}
#result-fold {
  right: 2%;
}
.icon-circle,
.icon-circle-2,
.icon-circle-3,
.icon-xmark {
  position: absolute;
}
.icon-circle {
  top: -60px;
  left: -65px;
  color: aliceblue;
  font-size: 220px;
}
.icon-circle-2 {
  top: -35px;
  left: -40px;
  color: aliceblue;
  font-size: 170px;
}
.icon-circle-3 {
  top: -50px;
  left: -55px;
  color: red;
  font-size: 200px;
}
.icon-xmark {
  top: -90px;
  left: -50px;
  color: blue;
  font-size: 300px;
}
